
:root {
    --content-leave-transition-duration: 300ms;
    --content-enter-transition-duration: 500ms;
    --content-enter-transition-starttime: 350ms;
}

@font-face {
    font-family: "m5x7";
    src: url(/about-me/static/media/m5x7.1c36fac5.ttf),
         url(/about-me/static/media/m5x7.b99dd26c.woff),
         url(/about-me/static/media/m5x7.1681d9b9.woff2),
         url(/about-me/static/media/m5x7.dc97bf00.svg#filename);
}

@font-face {
    font-family: "m6x11";
    src: url(/about-me/static/media/m6x11.cc8dfaa3.ttf),
         url(/about-me/static/media/m6x11.06395abf.woff),
         url(/about-me/static/media/m6x11.4b67f4ca.woff2),
         url(/about-me/static/media/m6x11.ec0d4e88.svg#filename);
}

.pixel-art {
    width: 128px;
    height: 128px;
    image-rendering: pixelated;
    image-rendering: -moz-crisp-edges;
    image-rendering: crisp-edges;
}
  
.header-text {
    font-family: "m6x11";
    font-size: 32px;
    font-weight: normal;
    font-style: normal;
}

.subheader-text {
    font-family: "m5x7";
    font-size: 32px;
    font-weight: normal;
    font-style: normal;
}

.header-padding {
    padding-top: 24px;
    padding-bottom: 32px;
}

